import React, { useState, useRef } from "react";
import "./CodeArea.css";
import "codemirror/theme/blackboard.css";
import "codemirror/theme/dracula.css";
import "codemirror/lib/codemirror.css";
import "codemirror/mode/xml/xml";
import "codemirror/mode/css/css";
import "codemirror/addon/hint/css-hint";
import "codemirror/addon/fold/foldgutter";
import "codemirror/addon/lint/css-lint";
import "codemirror/addon/wrap/hardwrap";
import "codemirror/addon/comment/comment";
import "codemirror/addon/hint/show-hint";
import "codemirror/addon/hint/html-hint";
import "codemirror/addon/edit/closebrackets";
import "codemirror/addon/edit/closetag";
// import "codemirror/addon/edit/continuelist";

import { Controlled as ControlledInput } from "react-codemirror2";

const CodeArea = (props) => {
  const [mode, setMode] = useState("xml");
  const [textHighlighter, setTextHighlighter] = useState("HTML");

  const styleHTMLTag = {
    opacity: textHighlighter === "HTML" ? 1 : 0.5,
    transition: "0.2s ease-in",
    borderTopLeftRadius: "0.4rem",
    borderTopRightRadius: "0.4rem",
    fontSize: textHighlighter !== "HTML" ? "14px" : "1rem",
  };
  const styleCSSTag = {
    opacity: textHighlighter === "CSS" ? 1 : 0.5,
    transition: "0.2s ease-in",
    borderTopLeftRadius: "0.4rem",
    borderTopRightRadius: "0.4rem",
    fontSize: textHighlighter !== "CSS" ? "14px" : "1rem",
  };
  return (
    <div className="code-area">
      <div className="code-area-header">
        <div className="language">
          <span
            onClick={() => {
              setMode("xml");
              setTextHighlighter("HTML");
            }}
            style={styleHTMLTag}
          >
            HTML
          </span>
          <span
            onClick={() => {
              setMode("css");
              setTextHighlighter("CSS");
            }}
            style={styleCSSTag}
          >
            CSS
          </span>
        </div>
      </div>
      <div className="code-mirror-wrapper">
        <ControlledInput
          onBeforeChange={(editor, data, value) => {
            mode === "xml" ? props.setData[0](value) : props.setData[1](value);
          }}
          value={mode === "xml" ? props.data[0] : props.data[1]}
          id={props.id}
          options={{
            smartIndent: true,
            spellcheck: true,
            mode: mode,
            lineNumbers: true,
            lineWrapping: true,
            lint: true,
            theme: mode === "xml" ? "dracula" : "blackboard",
            foldGutter: true,
            gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
            autoCloseTags: true,
            matchBrackets: true,
            autoCloseBrackets: true,
          }}
        />
      </div>
    </div>
  );
};

export default CodeArea;
