import React, { useState, useEffect } from "react";
import CodeEditor from "./CodeEditor.jsx";
const App = () => {
  const [deviceManager, setdeviceManager] = useState(false);
  useEffect(() => {
    if (window.innerWidth <= 800) setdeviceManager(true);
    else setdeviceManager(false);
  }, []);

  return (
    <>
      {!deviceManager ? (
        <CodeEditor />
      ) : (
        "Sorry, this website is only available on desktop devices."
      )}
    </>
  );
};

export default App;
