import React from "react";
import "./Navbar.css";
import SaveIcon from "@material-ui/icons/Save";
import Download from "@material-ui/icons/SaveAlt";
import CodeIcon from "@material-ui/icons/Code";
import ClearIcon from "@material-ui/icons/Clear";

const Navbar = (props) => {
  return (
    <div className="navbar">
      <div className="navbar-title">
        <CodeIcon
          color="secondary"
          fontSize="large"
          style={{ margin: "0rem 1rem" }}
        />
        <h1>CODE DIGGER</h1>
      </div>
      <div className="navbar-icons">
        <div className="icons" onClick={props.saveCode}>
          Save Code
          <SaveIcon
            color="primary"
            style={{ cursor: "pointer", margin: "0 0.5rem" }}
          />
        </div>
        <div className="icons" onClick={props.downloadCodeHandler}>
          Download Code
          <Download
            color="primary"
            style={{ cursor: "pointer", margin: "0 0.5rem" }}
          />
        </div>
        <div className="icons" onClick={props.clearCode}>
          Clear Code
          <ClearIcon
            color="secondary"
            style={{ cursor: "pointer", margin: "0 0.5rem" }}
          />
        </div>
      </div>
    </div>
  );
};

export default Navbar;
