import "./CodeEditor.css";
import { useState, useEffect } from "react";
import CodeAreaForJs from "./Components/CodeAreaForJs";
import CodeAreaHtmlCss from "./Components/CodeAreaHtmlCss";
import Navbar from "./Components/Navbar";
import Footer from "./Components/Footer";

function CodeEditor() {
  const [html, setHtml] = useState(
    !localStorage.getItem("html") ? "" : localStorage.getItem("html")
  );
  const [css, setCSS] = useState(
    !localStorage.getItem("css") ? "" : localStorage.getItem("css")
  );
  const [js, setJs] = useState(
    !localStorage.getItem("js") ? "" : localStorage.getItem("js")
  );
  const [DOM, setDOM_CODE] = useState("");

  const saveCodeHandler = () => {
    localStorage.setItem("html", html);
    localStorage.setItem("css", css);
    localStorage.setItem("js", js);
  };

  const clearCodeHandler = () => {
    localStorage.clear();
    setHtml("");
    setCSS("");
    setJs("");
    setDOM_CODE(`
    <html>
    <body>${html}</body>
    <style>${css}</style>
    <script>${js}</script>
    </html>
    `);
  };
  const downloadCodeHandler = () => {
    const element = document.createElement("a");
    const file = new Blob([DOM], {
      type: "text/plain;charset=utf-8",
    });
    element.href = window.URL.createObjectURL(file);
    console.log(element.href);
    element.download = "myFile.txt";
    document.body.appendChild(element);
    console.log(element.download);
    element.click();
    element.parentNode.removeChild(element);
  };

  useEffect(() => {
    setDOM_CODE(`<html>
<style>
${css}
</style>
<body>
${html}
</body>
<script>
${js}
</script>
</html>
      `);
  }, [html, css, js]);

  return (
    <>
      <Navbar
        saveCode={saveCodeHandler}
        clearCode={clearCodeHandler}
        downloadCodeHandler={downloadCodeHandler}
      />
      <div className="container">
        <div className="code-container">
          <CodeAreaHtmlCss
            data={[html, css]}
            setData={[setHtml, setCSS]}
            id={"html-code"}
          />

          <CodeAreaForJs
            data={js}
            setData={setJs}
            id={"js-code"}
            language="javascript"
          />
        </div>
        <div className="output">
          <iframe
            style={{
              height: "89%",
              width: "100%",
            }}
            srcDoc={DOM}
            sandbox="allow-scripts"
          />
          <Footer />
        </div>
      </div>
    </>
  );
}

export default CodeEditor;
